.catalog-action {
  padding-top: 35px;
  padding-bottom: 35px;
}

.catalog-action__wrapper {
  position: relative;
  .wrapper();
  min-height: 290px;
  background-image: url("@{img-address}catalog-action-foto.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(104, 183, 56, 0.85);
    position: absolute;
    left: 0;
    top: 0;
  }
}

.catalog-action__content {
    position: relative;
    width: 161px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 58px;
}

.catalog-action__img {
  display: block;
  position: relative;
  margin-bottom: 50px;
}

.catalog-action__slogan {
  position: relative;
  margin: 0;
  width: 161px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
}

@media(min-width: @tablet) {
  .catalog-action__wrapper {
    .wrapper-tablet();
    min-height: 200px;
    background-image: url("@{img-address}catalog-action-foto-tablet.png");
  }
  .catalog-action__content {
    width: 506px;
    flex-direction: row;
    justify-content: space-between;
  }
  .catalog-action__img {
    margin-bottom: 0;
  }
  .catalog-action__slogan {
    width: 165px;
    text-align: left;
  }
}

@media(min-width: @desktop) {
  .catalog-action {
    padding: 0;
  }
  .catalog-action__wrapper {
    width: 245px;
    min-height: 288px;
    background-image: url("@{img-address}catalog-action-foto-desktop.png")
  }
  .catalog-action__content {
    width: 161px;
    flex-direction: column;
    justify-content: space-between;
  }
  .catalog-action__img {
    margin-bottom: 46px;
  }
  .catalog-action__slogan {
    text-align: center;
  }
}

